import React, { useState, useEffect } from "react";
import myStyle from './Report.module.scss';


function Report({imageSrc}) {
  const [imageLoaded, setImageLoaded] = useState(false); // 이미지 로드 상태
  const [isKeyboardOpen, setIsKeyboardOpen] = useState(false);

  useEffect(() => {
    let initialHeight = window.innerHeight;

    const handleResize = () => {
      const currentHeight = window.innerHeight;
      // 키보드가 올라오면 높이가 감소
      setIsKeyboardOpen(currentHeight < initialHeight);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  useEffect(() => {
    // 이미지 로드 완료 확인
    const img = new Image();
    img.src = imageSrc;
    img.onload = () => setImageLoaded(true); // 이미지 로드 완료 시 상태 업데이트
  }, []);

  return (
    <div className={myStyle.Report}>
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        {imageLoaded ? (
          <img
            src={imageSrc}
            className={myStyle.imgSrc}
            // style={{
            //   width: '90%', // 높이에 맞추기 위해 넓이는 자동으로 설정
            //   height: 'auto', // 부모 div의 높이에 맞추기
            //   objectFit: 'cover',
            //   position: 'relative', // 위치 조정 가능
            //   top: '-1em', // 위로 이동 (값 조정 가능)
            
            //   maskImage:
            //   'linear-gradient(to top, rgba(150, 255, 150, 0.15) 10%, rgba(255, 255, 255, 1) 60%)',
            //   maskRepeat: 'no-repeat',
            // }}
            alt="Example"
          />

        ) : (
          <div style={{ width: '70%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            <p></p>
          </div>
        )}
        
      </div>

      <div className={myStyle.textContent}>
        그 사람이 행복할 때, 제가 행복했습니다.
        <br /> 그래서 그 사람은 언제 행복한지 물어봤습니다.
        <br /> 그러자 그 사람은 제가 행복할 때, 행복하다고 했습니다.
        <br />
        <br />
        {isKeyboardOpen?"":
        <div className={`${myStyle.mainContent} ${myStyle.coordinates}`}>서로 자신을 위해서 
          <br/> 상대가 <span className={myStyle.point}>행복</span>하도록 최선을 다하겠습니다.
        </div>}
        
      </div>
      
    </div>
  );
}

export default Report;
