import React, { useState , useEffect , useRef  } from "react";
import myStyle from './Main.module.scss';
import BankAccount from './content/BankAccount';
import Report from './content/Report';
import Directions from './content/Directions';
import Gallery from './content/Gallery';

import axios from 'axios';

function Main({imageSrc , activeSection , setActiveSection , param}) {
	// const [activeSection, setActiveSection] = useState(1); // 기본 섹션을 "schedule"로 설정
	  
  const [sender, setSender] = useState("");
	
  const [recipient, setRecipient] = useState(param=== "1"? "신부":"신랑"); // Default recipient
  const [message, setMessage] = useState(""); 
  const [phone, setPhone] = useState(""); 
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [ckBar, setCkBar] = useState(true); 
  const [ckSendMsg, setCkSendMsg] = useState(false); 
  // const [ckBar, setCkBar] = useState(true); 

  const handleChange = () => {
    setCkBar(!ckBar); // 선택된 라디오 버튼 값 업데이트
  };
  const handleRecipientChange = (e) => setRecipient(e.target.value);
  const handleSenderChange = (e) => setSender(e.target.value);
  const handlePhoneChange = (e) => setPhone(e.target.value);

	const handleSubmit = async () => {
    if (isSubmitting) return; // 이미 전송 중이면 동작하지 않음
      setIsSubmitting(true); // 전송 중 상태로 변경
			setCkBar(false);
    // 유효성 검사
    if (!sender.trim()) {
      alert("보내는 사람을 입력해주세요!");
			setIsSubmitting(false); // 전송 중 상태로 변경
      return;
    }
  
		// if (!phone.trim()) {
		// 	alert("전화번호를 입력해주세요!");
		// 	return;
		// }
		
		if (phone.length < 10) {
			alert("전화번호를 확인부탁드립니다.");
			setIsSubmitting(false); // 전송 중 상태로 변경
			return;
		}
  
    // Firebase Functions 트리거 URL
    const url = process.env.REACT_APP_MSG_URL;

    
    // 요청 데이터
    const data = {
      sender: sender.trim(),
      message: phone.trim(),
      recipient: recipient.trim(),
      msgType: "참석",
    };
  
    try {
      // POST 요청
      const response = await axios.post(url, data);
  
      // 성공 응답 처리
      if (response.status === 200) {
        alert("참석 확인되었습니다.");
				setCkSendMsg(true);
        // 입력 필드 초기화
        // setSender("");
        // setMessage("");
        //setRecipient(""); // 필요 시 활성화
      } else {
        // alert("예상치 못한 응답이 반환되었습니다.");
      }
    } catch (error) {
      // 오류 처리
      console.error("Error sending message:", error);
      // alert("메시지 전송에 실패했습니다. 네트워크를 확인해주세요.");
    }finally{ setIsSubmitting(false); }

  };


  return (
		<div className={myStyle.main}>
		{activeSection!="1"?"": ckSendMsg?"":
		<div className={`${myStyle.container} ${"no-slide-area"}`}>
			{!ckBar?"":
			<div className={myStyle.barTitle}>식사준비를 위해 참석여부를 알려주세요 </div>
			}
			<div className={`${myStyle.ckBar} ${!ckBar? myStyle.ckbarSize:""} `}>
				{/* <div className={myStyle.barTitle}>식사준비를 위해 참석여부를 알려주세요 </div> */}
 
				<div className={myStyle.barBt} onClick={handleChange}>  
				{!ckBar?"참석등록":"눌러서창접기"}
				</div>

				{!ckBar?"":
				<div className={myStyle.bar}> 
					<form className={myStyle.form}>
						<div className={myStyle.group}>
							{/* 이름 입력 */}
							<div className={myStyle.inputGroup}>
								<label htmlFor="name">성함</label>
								<input type="text" id="name" name="name" 
								value={sender}
								onChange={handleSenderChange}
								placeholder="성함" />
								<label htmlFor="phone">연락처<span style={{fontSize:"0.7em"}}>('-' 빼고 번호만 입력해 주세요)</span></label>
								<input type="tel" id="phone" name="phone" 
								value={phone} onChange={handlePhoneChange}
								maxLength="11"
								// 숫자만 허용
								onInput={(e) => {e.target.value = e.target.value.replace(/[^0-9]/g, "");}}
								placeholder="01012341234" />
							</div>
							<div>
									{param === "1" ? 
									<div style={{margin:"1em"}}>
										<label style={{width:"100%", lineHeight:"1.4em"}}>
											<input 
													type="radio"
													name="side" 
													value="신랑"
													checked={recipient === "신랑"}
													onChange={handleRecipientChange}
													style={{ marginRight: " .5em", verticalAlign:"middle" }}
											/>
											신랑측손님
										</label>
										<br></br>
										<label style={{width:"100%", lineHeight:"1.4em"}}>
											<input 
													type="radio"
													name="side" 
													value="신부"
													checked={recipient === "신부"}
													onChange={handleRecipientChange}
													style={{ marginRight: " .5em", verticalAlign:"middle" }}
											/>
											신부측손님
										</label>	
									</div>
									
								: <div style={{margin:"1em"}}>
										<label style={{width:"100%", lineHeight:"1.4em"}}>
											<input 
													type="radio"
													name="side" 
													value="신랑"
													checked={recipient === "신랑"}
													onChange={handleRecipientChange}
													style={{ marginRight: " .5em", verticalAlign:"middle" }}
											/>
											신랑측손님
										</label>
										<br></br>	
										<label style={{width:"100%", lineHeight:"1.4em"}}>
											<input 
													type="radio"
													name="side" 
													value="신부"
													checked={recipient === "신부"}
													onChange={handleRecipientChange}
													style={{ marginRight: " .5em", verticalAlign:"middle" }}
											/>
											신부측손님
										</label>	
									</div>
									}											
								</div>
							<div className={`${myStyle.inputGroup}`}>
								<div type="submit" className={`${myStyle.bt2}`} onClick={handleSubmit}>눌러서참석등록</div>
							</div>
						</div>
						{/* 신랑측/신부측 선택 */}
						{/* 전송 버튼 */}
						{/* <div className={myStyle.inputGroup}>
							<button type="submit">참 석 등 록</button>
						</div> */}
					</form>
				</div>
			}
			</div>

		</div>

			}
			<div className={myStyle.title}>
				<div className={myStyle.buttonGroup}>
					<button className={`${myStyle.customButton} ${activeSection === 1 ? myStyle.selected : ""}`} onClick={() => setActiveSection(1)} >모시는 <span className={myStyle.point}>글</span></button>
					<button className={`${myStyle.customButton} ${activeSection === 2 ? myStyle.selected : ""}`} onClick={() => setActiveSection(2)}>오시는 <span className={myStyle.point}>길</span></button>
					<button className={`${myStyle.customButton} ${activeSection === 3 ? myStyle.selected : ""}`} onClick={() => setActiveSection(3)}>전하는 <span className={myStyle.point}>곳</span></button>
				</div>
			</div>
			<div className={myStyle.content}>
				{/*  화면 출력 */}
				{activeSection === 1 && <Report imageSrc={imageSrc}/>}
				{activeSection === 2 && <Directions/>}
				{activeSection === 3 && <BankAccount 
				sender={sender} setSender={setSender} 
				recipient={recipient}  setRecipient={setRecipient} 
				message={message}  setMessage={setMessage} 
				param={param} 
				/>}
				{activeSection === 4 && <Gallery/>}
		
			</div>

    </div>

  );
}


export default Main;
