import React from "react";
import myStyle from './Haeder.module.scss';

function Haeder() {
  return (
		<div className={myStyle.Haeder}>
			<div className={myStyle.content}>
				저희 결혼식에 초대합니다.<br/>
				오셔서 자리를 빛내 주시면 <span className={myStyle.point}>감사하겠습니다</span>.
			</div>
    </div>
  );
}

export default Haeder;
