import { getStorage, ref, uploadBytesResumable, getDownloadURL ,  uploadBytes } from 'firebase/storage';
import { storage  , auth, db } from "../../firebase";

// 이미지 리사이즈 함수
const resizeImage = (file, maxSize = 100) => {
	return new Promise((resolve, reject) => {
		const img = new Image();
		const reader = new FileReader();
		console.log("resizeImage --");
		reader.onload = () => {
			img.onload = () => {
				const canvas = document.createElement("canvas");
				const ctx = canvas.getContext("2d");

				// 이미지 비율에 맞게 크기 계산 (100MB 이하로 제한하기 위해 크기 조정)
				const maxWidth = 1024; // 최대 너비 (예시로 1024px로 설정)
				const maxHeight = 1024; // 최대 높이 (예시로 1024px로 설정)
				
				let width = img.width;
				let height = img.height;

				// 비율을 맞추기 위해 크기 조정
				if (width > height) {
					if (width > maxWidth) {
						height = (height * maxWidth) / width;
						width = maxWidth;
					}
				} else {
					if (height > maxHeight) {
						width = (width * maxHeight) / height;
						height = maxHeight;
					}
				}

				canvas.width = width;
				canvas.height = height;

				// 이미지 그리기
				ctx.drawImage(img, 0, 0, width, height);

				// 새로 리사이즈된 이미지를 Blob 형태로 변환
				canvas.toBlob((blob) => {
					resolve(blob);
				}, "image/jpeg", 0.8); // 0.8은 이미지 품질 (0~1 사이 값)
			};

			img.src = reader.result;
		};

		reader.readAsDataURL(file);
	});
};

// 이미지 업로드 함수
const uploadImage = async (image) => {
	console.log("uploadImage In")
  if (!image) return null;
	console.log("uploadImage 이미지 존재 ")

  const storageRef = ref(storage, `images/${image.name}`);  // 'images' 폴더에 저장
  const uploadTask = uploadBytesResumable(storageRef, image);

  return new Promise((resolve, reject) => {
    uploadTask.on(
      'state_changed',
      null,
      (error) => reject(error), // 에러 처리
      async () => {
        try {
          const downloadURL = await getDownloadURL(uploadTask.snapshot.ref);
          resolve(downloadURL); // 다운로드 URL 반환
        } catch (error) {
          reject(error);
        } finally{
					console.log("uploadImage 완료 ")
				}
      }
    );
  });
};
	

export { uploadImage, resizeImage  };
