// firebase.js or firebaseConfig.js

import { initializeApp } from "firebase/app";
import { getAuth } from "firebase/auth";  // Firebase 인증
import { getAnalytics } from "firebase/analytics";
import { getStorage } from "firebase/storage"; // Firebase Storage
import { getFirestore } from "firebase/firestore"; // Firestore (필요한 경우)

const firebaseConfig = {
  apiKey: process.env.REACT_APP_API_KEY,
  authDomain: process.env.REACT_APP_PROJECT_ID+".firebaseapp.com",
  projectId: process.env.REACT_APP_PROJECT_ID,
  storageBucket: process.env.REACT_APP_PROJECT_ID+".firebasestorage.app",
  messagingSenderId: process.env.REACT_APP_MSG_ID,
  appId: process.env.REACT_APP_APP_ID,
  measurementId: process.env.REACT_APP_MEASUREMENT_ID,
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

// Initialize Firebase Analytics (optional)
const auth = getAuth(app);
const analytics = getAnalytics(app);
const storage = getStorage(app);
const db = getFirestore(app); // Firestore

export { app, auth , analytics ,storage , db };
