import React, { useState } from "react";
import { useKeenSlider } from "keen-slider/react";
import "keen-slider/keen-slider.min.css";

import myStyle from "./Gallery.module.scss";

import imgWedding01 from 'assets/images/gallery/1_웨딩.jpg';
import imgWedding02 from 'assets/images/gallery/2_웨딩.jpg';
import imgWedding03 from 'assets/images/gallery/3_웨딩.jpg';
import imgWedding04 from 'assets/images/gallery/4_웨딩.jpg';
import imgWedding05 from 'assets/images/gallery/5_웨딩.jpg';
import imgWedding06 from 'assets/images/gallery/6_웨딩.jpg';
import img01 from 'assets/images/gallery/11_베트남.jpg';
import img02 from 'assets/images/gallery/12_베트남.jpg';
import img03 from 'assets/images/gallery/13_베트남.jpg';
import img04 from 'assets/images/gallery/14_베트남.jpg';
import img05 from 'assets/images/gallery/15_베트남.jpg';
import img06 from 'assets/images/gallery/16_베트남.jpg';
import img07 from 'assets/images/gallery/17_전체.jpg';
import imgVoid from 'assets/images/gallery/빈공간.jpg';


const images = [
  { id: 1, src: imgWedding01, alt: "Image 1" },
  { id: 4, src: imgWedding04, alt: "Image 4" },
  { id: 3, src: imgWedding03, alt: "Image 3" },
  { id: 5, src: imgWedding05, alt: "Image 5" },
  { id: 2, src: imgWedding02, alt: "Image 2" },
  { id: 6, src: imgWedding06, alt: "Image 6" },
  { id: 7, src: img01, alt: "Image 7" },
  { id: 8, src: img02, alt: "Image 8" },
  { id: 9, src: img03, alt: "Image 9" },
  // { id: 101, src: imgVoid, alt: "Image 101" },
  { id: 10, src: img04, alt: "Image 10" },
  { id: 11, src: img05, alt: "Image 11" },
  { id: 12, src: img06, alt: "Image 12" },
  { id: 13, src: img07, alt: "Image 13" },
];

const Gallery = () => {
  const [sliderRef] = useKeenSlider({
    loop: true,
    mode: "snap",
    slides: { perView: 1, spacing: 0 },
  });

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalImage, setModalImage] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(0);

  const openModal = (imageSrc) => {
    setModalImage(imageSrc);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setModalImage(null);
  };
  // Function to get random image size between 20px and 80px
  const getRandomSize = () => {
    return Math.floor(Math.random() * (80 - 20 + 1)) + 20; // Random size between 20px and 80px
  };
    // Function to navigate to the previous image
    const prevImage = () => {
      const prevIndex = (currentIndex - 1 + images.length) % images.length;
      setModalImage(images[prevIndex].src);
      setCurrentIndex(prevIndex);
    };
  
    // Function to navigate to the next image
    const nextImage = () => {
      const nextIndex = (currentIndex + 1) % images.length;
      setModalImage(images[nextIndex].src);
      setCurrentIndex(nextIndex);
    };

  return (
    <div className={`${myStyle.gallery} ${"no-slide-area"}`} >
      <div className={myStyle.title}>추억 사진관</div>
      <div className={myStyle.imgBox} >
        {images.map((image) => (
          <div key={image.id} className={myStyle.imgCol} 
          onClick={() => openModal(image.src)}>
            <img 
              src={image.src} 
              alt={image.alt} 
              className={myStyle.imgType} 
              // style={{ width: '15vw', height: 'auto' }} // 고정된 크기
            />
          </div>
        ))}
      </div>


      {isModalOpen && (
        <div className={`${myStyle.modal} ${"no-slide-area"}`} onClick={closeModal}>
          <div className={myStyle.modalContent} onClick={(e) => e.stopPropagation()}>
            <button className={myStyle.prevBtn} onClick={prevImage}>&lt;</button>
            <img src={modalImage} alt="Enlarged" />
            <button className={myStyle.nextBtn} onClick={nextImage}>&gt;</button>
            <span className={myStyle.closeBtn} onClick={closeModal}>&times;</span>
          </div>
        </div>
      )}
    </div>
  );
};

export default Gallery;
