import React, { useEffect, useState } from 'react';
import myStyle from './MenuPage.module.scss';
import { motion, AnimatePresence } from "framer-motion";

const MenuPage = ({ setMenuPage , activeSection , setActiveSection}) => {


	const activeSet = async (value) => {
		setActiveSection(value);
		setMenuPage(false);
	}

  return (
    <div className={myStyle.container }>
			<div className={myStyle.bkImg}>
			</div>

      <div className={myStyle.bk}>
				{/* 모달 닫기 버튼 */}

			<div className={myStyle.title}>
			<button
				onClick={() => setMenuPage(false)}
				style={{
					position: "relative",
					bottom: "5px",
					// left: "45vw",
					backgroundColor: "black",
					color: "white",
					width:"100%",
					height:"10%",
					border: "none",
					fontSize: "1.8em",
					cursor: "pointer",
				}}
			>
				닫기
			</button>
				<div className={myStyle.buttonGroup}>
					<button className={`${myStyle.customButton} ${activeSection === 1 ? myStyle.selected : ""}`} onClick={() => activeSet(1)} >모시는 <span className={myStyle.point}>글</span></button>
					{/* <span className={myStyle.separator}> / </span> */}
					<button className={`${myStyle.customButton} ${activeSection === 2 ? myStyle.selected : ""}`} onClick={() => activeSet(2)}>오시는 <span className={myStyle.point}>길</span></button>
					{/* <span className={myStyle.separator}> / </span> */}
					<button className={`${myStyle.customButton} ${activeSection === 3 ? myStyle.selected : ""}`} onClick={() => activeSet(3)}>전하는 <span className={myStyle.point}>곳</span></button>
				</div>
			</div>

      </div>
    </div>
  );
};

export default MenuPage;
