import React, { useState , useEffect } from "react";
import kakaoIcon from "./icons/kakaomap_vertical_ko.png"; // 아이콘 이미지 경로
import naverIcon from "./icons/Map_Service_Icon.png";
import tmapIcon from "./icons/tmap_icon.jpg";
import mapImg from 'assets/images/약도.jpg';

const MapLinks = ({ latitude, longitude, placeName }) => {
  const [isModalOpen, setIsodalOpen] = useState(false); // 모달 상태 관리
  const [isWideScreen, setIsWideScreen] = useState(false);
  // 서비스 별 URL 생성
  const kakaoUrl = `https://map.kakao.com/link/to/${placeName},${latitude},${longitude}`;
  const naverUrl = `nmap://navigation?dlat=${latitude}&dlng=${longitude}&dname=${placeName}`;
  const tmapUrl = `tmap://route?goalname=Location&goalx=${longitude}&goaly=${latitude}`;

  // 모달 열기/닫기 함수
  const toggleModal = () => {
    setIsodalOpen(!isModalOpen);
  };


  useEffect(() => {
    const handleResize = () => {
      setIsWideScreen(window.innerWidth / window.innerHeight > 1); // 화면 비율 계산
    };

    handleResize(); // 초기 실행
    window.addEventListener('resize', handleResize); // 크기 변경 감지
    return () => window.removeEventListener('resize', handleResize); // 이벤트 정리
  }, []);

  const styles = {
    container: {
      backgroundColor: '#f9f9f9',
      padding: '20px',
      borderRadius: '10px',
      fontFamily: 'Cafe24Shiningstar, sans-serif',
      boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
      maxWidth: '600px',
      margin: '20px auto',
      textAlign: 'left',
    },
    title: {
      textAlign: 'center',
      fontSize: '1.8rem',
      marginBottom: '1rem',
      color: '#4caf50',
    },
    section: {
      marginBottom: '1.5rem',
    },
    heading: {
      fontSize: '1.4rem',
      color: '#333',
      marginBottom: '0.5rem',
    },
    text: {
      fontSize: '1rem',
      lineHeight: '1.6',
      color: '#666',
    },
    highlight: {
      color: '#4caf50',
      fontWeight: 'bold',
    },
    bus: {
      color: '#af4c50',
      fontWeight: 'bold',
    },
  };

  return (
    <div style={{ display: "flex",
      flexDirection: 'column',
      height:"100%", justifyContent:"flex-start", gap:"1em", paddingTop:"0.5em"

    }}>
      {/* Kakao Map */}

        <a href={kakaoUrl} target="_blank" rel="noopener noreferrer">
          <img
            src={kakaoIcon}
            alt="Kakao Map"
            style={{ width: "50px", height: "auto", cursor: "pointer" }}
          />
          
        </a>

      {/* Naver Map */}
      <a href={naverUrl} target="_blank" rel="noopener noreferrer">
        <img
          src={naverIcon}
          alt="Naver Map"
          style={{ width: "50px", height: "auto", cursor: "pointer" }}
        />
      </a>
      

      {/* TMap */}
      <a href={tmapUrl} target="_blank" rel="noopener noreferrer">
        <img
          src={tmapIcon}
          alt="TMap"
          style={{ width: "50px", height: "auto", cursor: "pointer" }}
        />
      </a>

      <button
        onClick={toggleModal}
        style={{
          width: "50px", height: "50px",
          fontSize: " clamp(15px, 1em, 40px)",
          cursor: "pointer",
          color:"rgba(0,0,0,0.9)",
          borderRadius:"3px",
          marginBottom: "1em",
          border: "1px solid rgba(145,136,57,0.3)", // border를 제거하고 box-shadow로 대체
          backgroundColor: "rgba(145,136,57,0)",
        }}
      >
        대중
        <br/>교통
      </button>

      {isModalOpen && (
        <div
          onClick={toggleModal} 
          style={{
            position: "fixed",
            top: 0, left: 0,
            width: "100%", height: "100%",
            justifyContent: "center", alignItems: "center",
            backgroundColor: "rgba(0, 0, 0, 0.5)",
            display: "flex",
            zIndex: 1000,
          }}
        >
          <div
            onClick={(e) => e.stopPropagation()} // 클릭 이벤트 전파 방지
            className="no-slide-area"
            style={{
              backgroundColor: "white",
              padding: "20px",
              borderRadius: "10px",
              width: "90%",
              fontSize:"1.3em",
              textAlign: "left",
              position: "relative",
            }}
          >

            {/* 모달 닫기 버튼 */}
            <button
              onClick={toggleModal}
              style={{
                position: "absolute",
                top: "10px",right: "10px",
                backgroundColor:"white",
                border: "none",
                fontSize: "18px",
                cursor: "pointer",
              }}>❌</button> 

      <h2 style={styles.title}>웨딩홀 교통 안내</h2>
        <div style={styles.section}>
          <h3 style={styles.heading}>🚍 웨딩홀 순환 버스 (15분 간격)</h3>
          <p style={styles.text}>
            <span style={styles.highlight}>이마트 버스승강장</span>에서 아름다운 웨딩홀 왕복 운행
          </p>
        </div>
        <div style={styles.section}>
          <h3 style={styles.heading}>🛤️ [오송역 ▶ 터미널]</h3>
          <p style={styles.text}>
            <span style={styles.highlight}>503 버스 이용</span> : 오송역5 정류장 승차 ▶ <span style={styles.bus}>고속버스터미널</span> 정류장 하차
          </p>
        </div>
        <div style={styles.section}>
          <h3 style={styles.heading}>🛤️ [터미널 ▶ 이마트]</h3>
          <p style={styles.text}>
            <span style={styles.highlight}>815 버스 이용</span> : <span style={styles.bus}>고속버스터미널</span> 정류장 승차 ▶ 이마트 정류장 하차 ▶ 웨딩홀 셔틀버스
          </p>
        </div>


          <img
            src={mapImg}
            style={{
              width: isWideScreen ? 'auto' : '100%',
              height: isWideScreen ? '100%' : 'auto',
              objectFit: 'cover',
              position: 'relative', // 위치 조정 가능

            }}
            alt="Example"
          />

            {/* 지도 링크 */}
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                gap: "1em",
              }}
            >
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default MapLinks;
